          appearance: none;
  margin: 0;
  outline: none;
  outline-width: 2px;
  position: absolute;
  inset: 0;
  border-radius: inherit;
}

.fui-ThemePanelSwatch {
  width: var(--space-5);
  height: var(--space-5);
  border-radius: 100%;
}

.fui-ThemePanelSwatchInput {
  outline-offset: 2px;
}

.fui-ThemePanelSwatchInput:where(:checked) {
    outline-style: solid;
    outline-color: var(--gray-12);
  }

.fui-ThemePanelSwatchInput:where(:focus-visible) {
    outline-style: solid;
    outline-color: var(--accent-9);
  }

.fui-ThemePanelRadioCard {
  border-radius: var(--radius-1);
  box-shadow: 0 0 0 1px var(--gray-7);
}

.fui-ThemePanelRadioCardInput {
  outline-offset: -1px;
}

.fui-ThemePanelRadioCardInput:where(:checked) {
    outline-style: solid;
    outline-color: var(--gray-12);
  }

.fui-ThemePanelRadioCardInput:where(:focus-visible) {
    background-color: var(--accent-a3);
    outline-style: solid;
    outline-color: var(--accent-9);
  }

.fui-Shine {
  position: relative;
  -webkit-user-select: none;
          user-select: none;
}

.fui-ShineSvgFilter {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
}
